import React from "react";
import OptionsCard from "./OptionsCard";
import LinkCard from "./LinkCard";
import {
  MDBNavbar,
  MDBNavbarBrand,
  MDBNavbarNav,
  MDBNavbarToggler,
  MDBCollapse,
} from "mdbreact";
import { BrowserRouter as Router } from "react-router-dom";
import data from "../Info.json";

class Navigation extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      collapse: false,
    };
    this.onClick = this.onClick.bind(this);
  }

  onClick() {
    this.setState({
      collapse: !this.state.collapse,
    });
  }

  render() {
    function createOptionsCard(option) {
      return (
        <OptionsCard
          key={option.key}
          className={option.className}
          to={option.to}
          link={option.link}
        />
      );
    }
    function createLinksCard(Link) {
      return <LinkCard key={Link.key} icon={Link.icon} />;
    }
    const container = { height: 1300 };
    const links = data.Navigation;
    return (
      <div>
        <Router>
          <header>
            <MDBNavbar color={links.colour} dark expand={links.expansion}>
              <MDBNavbarBrand href="/">
                <strong id={links.key0}>{links.text}</strong>
              </MDBNavbarBrand>
              <MDBNavbarToggler onClick={this.onClick} />
              <MDBCollapse isOpen={this.state.collapse} navbar>
                <MDBNavbarNav left>
                  {data.Options.map(createOptionsCard)}
                </MDBNavbarNav>
                <MDBNavbarNav right>
                  {data.Links.map(createLinksCard)}
                </MDBNavbarNav>
              </MDBCollapse>
            </MDBNavbar>
          </header>
        </Router>
      </div>
    );
  }
}

export default Navigation;
