import React from "react";
import { MDBNavItem, MDBNavLink } from "mdbreact";
import { HashLink } from "react-router-hash-link";
function OptionsCard(props) {
  return (
    <MDBNavItem>
      <MDBNavLink className={props.className} to="#">
        <HashLink smooth to={props.to}>
          {props.link}
        </HashLink>
      </MDBNavLink>
    </MDBNavItem>
  );
}
export default OptionsCard;
