import React from "react";
import Navigation from "./Navigation";
import Slider from "./Slider";
import data from "../Info.json";
import { MDBBadge, MDBContainer } from "mdbreact";

function Navbar() {
  const heading1 = data.heading.heading1;

  return (
    <div>
      <Navigation />
      <MDBContainer>
        <h2 className={heading1.classes}>
          <MDBBadge color={heading1.colour} id={heading1.key0}>
            {heading1.heading}
          </MDBBadge>
        </h2>
      </MDBContainer>
      <Slider />
    </div>
  );
}
export default Navbar;
