import React from "react";
import {
  MDBCarouselCaption,
  MDBCarouselItem,
  MDBView,
  MDBMask,
} from "mdbreact";
import data from "../Info.json";
function Slides(props) {
  const Slide = data.Slides;
  return (
    <MDBCarouselItem itemId={props.slide}>
      <MDBView>
        <img className={Slide.className} src={props.source} alt={Slide.alt} />
        <MDBMask overlay={Slide.overlay} />
      </MDBView>
      <MDBCarouselCaption>
        <h3 className={Slide.className1} id={Slide.key0}>
          {props.heading}
        </h3>
        <p id={Slide.key1}> {props.content}</p>
      </MDBCarouselCaption>
    </MDBCarouselItem>
  );
}
export default Slides;
