import { MDBContainer, MDBRow } from "mdb-react-ui-kit";
import React from "react";
import MidSecCard from "./MidSecCard";
import kids from "../images/Toddler's Section/kids.jpeg";
import donate from "../images/Donate/donate.jpg";
import data from "../Info.json";

function CreateMidSecCard(Midsec, index) {
  var sources = donate;
  if (index === 0) {
    sources = donate;
  } else {
    sources = kids;
  }
  return (
    <MidSecCard
      key={Midsec.key}
      heading={Midsec.heading}
      source={sources}
      title={Midsec.title}
      content={Midsec.content}
      button={Midsec.button}
    />
  );
}
function MidSection() {
  const Mid = data.MidSection;
  return (
    <MDBContainer>
      <MDBRow className={Mid.classes}>
        {data.MidSecList.map(CreateMidSecCard)}
      </MDBRow>
    </MDBContainer>
  );
}
export default MidSection;
