import React from "react";
import {
  MDBCard,
  MDBCardImage,
  MDBCardBody,
  MDBCardTitle,
  MDBCardText,
  MDBCol,
  MDBBtn,
  MDBBadge,
  MDBContainer,
} from "mdb-react-ui-kit";
import data from "../Info.json";
function MidSecCard(props) {
  const MidSecCard = data.MidSecCard;
  return (
    <div>
      <MDBContainer>
        <h2 className={MidSecCard.className}>
          <MDBBadge
            color={MidSecCard.color}
            className={MidSecCard.className1}
            id={MidSecCard.key0}
          >
            {props.heading}
          </MDBBadge>
        </h2>
      </MDBContainer>
      <MDBCol>
        <MDBCard>
          <MDBCardImage
            src={props.source}
            alt={MidSecCard.alt}
            position={MidSecCard.position}
          />
          <MDBCardBody>
            <MDBCardTitle id={MidSecCard.key1}>{props.title}</MDBCardTitle>
            <MDBCardText id={MidSecCard.key2}>{props.content}</MDBCardText>
            <MDBBtn
              id={MidSecCard.key3}
              color={MidSecCard.color1}
              size={MidSecCard.size}
            >
              {props.button}
            </MDBBtn>
          </MDBCardBody>
        </MDBCard>
      </MDBCol>
    </div>
  );
}
export default MidSecCard;
