import React from "react";
import TicketCard from "./TicketCard";
import { MDBRow, MDBContainer, MDBBadge } from "mdbreact";
import first from "../images/Conservation/koala.jpg";
import data from "../Info.json";

function createTicketCard(tickets) {
  return (
    <TicketCard
      key={tickets.key}
      source={first}
      heading={tickets.heading}
      price={tickets.price}
    />
  );
}

function Tickets() {
  const heading5 = data.heading.heading5;

  const Tickets = data.Tickets;

  return (
    <MDBContainer>
      <MDBContainer>
        <h2 className={heading5.classes}>
          <MDBBadge
            color={heading5.colour}
            className={heading5.extraClass}
            id={heading5.key0}
          >
            {heading5.heading}
          </MDBBadge>
        </h2>
      </MDBContainer>
      <p className={Tickets.pclasses} id={Tickets.pkey}>
        {Tickets.pcontent}
      </p>
      <section className={Tickets.sclasses}>
        <MDBRow>{data.TicketsFinal.map(createTicketCard)}</MDBRow>
      </section>
    </MDBContainer>
  );
}
export default Tickets;
