import React from "react";
import ConserveCard from "./ConserveCard";
import { MDBCardGroup, MDBBadge, MDBContainer } from "mdbreact";
import first from "../images/Conservation/koala.jpg";
import second from "../images/Conservation/gorilla.jpg";
import third from "../images/Conservation/tiger.jpg";
import data from "../Info.json";

function createConserveCard(conserve, index) {
  var sources = first;
  if (index === 0) {
  } else if (index === 1) {
    sources = second;
  } else {
    sources = third;
  }
  return (
    <ConserveCard
      key={conserve.key}
      source={sources}
      alternative={conserve.alternative}
      overlays={conserve.overlays}
      tags={conserve.tags}
      key0={conserve.key0}
      title={conserve.title}
      key1={conserve.key1}
      key2={conserve.key2}
      content={conserve.content}
      key3={conserve.key3}
      colour={conserve.colour}
      sizes={conserve.sizes}
    />
  );
}

function Conservation() {
  const conserve = data.heading.heading2;

  return (
    <div>
      <MDBContainer>
        <h2 className={conserve.classes}>
          <MDBBadge
            color={conserve.colour}
            className={conserve.extraClass}
            id={conserve.key0}
          >
            {conserve.heading}
          </MDBBadge>
        </h2>
      </MDBContainer>
      <MDBCardGroup>{data.Conservation.map(createConserveCard)}</MDBCardGroup>
    </div>
  );
}
export default Conservation;
