import React from "react";
import ListTicketCard from "./ListTicketCard";
import { MDBCol, MDBCard, MDBCardBody, MDBBtn } from "mdbreact";
import data from "../Info.json";

function createListTicket(lists) {
  return (
    <ListTicketCard
      key={lists.key}
      quantity={lists.quantity}
      text={lists.text}
    />
  );
}
function TicketCard(props) {
  const ticketcard = data.TicketCard;
  return (
    <MDBCol
      lg={ticketcard.lg}
      md={ticketcard.md}
      className={ticketcard.classes}
    >
      <MDBCard
        className={ticketcard.classes1}
        style={{
          backgroundImage: `url(${props.source})`,
        }}
      >
        <div className={ticketcard.classes2}>
          <MDBCardBody>
            <h5 id={ticketcard.key0}>{props.heading}</h5>
            <div className={ticketcard.classes3}>
              <h2 className={ticketcard.classes4}>{props.price}</h2>
            </div>
            <ul className={ticketcard.classes5}>
              {data.TicketList.map(createListTicket)}
            </ul>
            <MDBBtn outline color={ticketcard.colour} id={ticketcard.key1}>
              {ticketcard.btntext}
            </MDBBtn>
          </MDBCardBody>
        </div>
      </MDBCard>
    </MDBCol>
  );
}
export default TicketCard;
