import React from "react";
import {
  MDBCard,
  MDBCardTitle,
  MDBBtn,
  MDBCardImage,
  MDBCardText,
  MDBCardBody,
} from "mdbreact";
function ConserveCard(props) {
  return (
    <MDBCard>
      <MDBCardImage
        src={props.source}
        alt={props.alternative}
        top
        hover
        overlay={props.overlays}
      />
      <MDBCardBody>
        <MDBCardTitle tag={props.tags} id={props.key0}>
          {props.title}
        </MDBCardTitle>
        <MDBCardText id={props.key1} id={props.key2}>
          {props.content}
        </MDBCardText>
        <MDBBtn id={props.key3} color={props.colour} size={props.sizes}>
          read more
        </MDBBtn>
      </MDBCardBody>
    </MDBCard>
  );
}
export default ConserveCard;
