import React from "react";
import { MDBCol, MDBContainer, MDBRow, MDBFooter } from "mdbreact";
import FooterSocial from "./FooterSocial";
import data from "../Info.json";

function createFooterSocial(socials) {
  return <FooterSocial className={socials.className} />;
}
function Footer() {
  const footer = data.Footer;
  return (
    <MDBFooter color={footer.color} className={footer.className}>
      <MDBContainer className={footer.className1}>
        <MDBRow className={footer.className2}>
          <MDBCol
            md={footer.md}
            lg={footer.lg}
            xl={footer.xl}
            className={footer.className3}
          >
            <MDBCol>
              <div>
                <ul className={footer.className4}>
                  <li>Hours</li>
                  <li>Weekdays 9:30am–4pm</li>
                  <li>Weekends 9:30am–5pm</li>
                  <br />
                  <li className={footer.className5}>Location</li>
                  <br />
                  <li className={footer.className6}>6800 Zoo Drive</li>
                  <li className={footer.className7}>New York City, MO 64132</li>
                </ul>
              </div>
            </MDBCol>
          </MDBCol>

          <hr className={footer.className8} />
          <MDBCol
            md={footer.md1}
            lg={footer.lg1}
            xl={footer.xl1}
            className={footer.className9}
          >
            <h6 className={footer.className10}>{footer.h6}</h6>
            <p>
              <a href="#!">{footer.a1}</a>
            </p>
            <p>
              <a href="#!">{footer.a2}</a>
            </p>
            <p>
              <a href="#!">{footer.a3}</a>
            </p>
            <p>
              <a href="#!">{footer.a4}</a>
            </p>
          </MDBCol>
          <hr className={footer.className11} />
          <MDBCol
            md={footer.md2}
            lg={footer.lg2}
            xl={footer.xl2}
            className={footer.className12}
          >
            <h6 className={footer.className13}>{footer.h62}</h6>
            <p>
              <i className={footer.className14} /> {footer.p1}
            </p>
            <p>
              <i className={footer.className15} /> {footer.p2}
            </p>
            <p>
              <i className={footer.className16} /> {footer.p3}
            </p>
            <p>
              <i className={footer.className17} /> {footer.p4}
            </p>
          </MDBCol>
        </MDBRow>
        <hr />
        <MDBRow className={footer.className18}>
          <MDBCol md={footer.md3} lg={footer.lg3}>
            <p className={footer.className19}>
              &copy; {new Date().getFullYear()} {footer.copyright}:{" "}
              <a href={footer.href}> {footer.text} </a>
            </p>
          </MDBCol>
          <MDBCol
            md={footer.md4}
            lg={footer.lg4}
            className={footer.className20}
          >
            <div className={footer.className21}>
              <ul className={footer.className22}>
                {data.FooterSocial.map(createFooterSocial)}
              </ul>
            </div>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    </MDBFooter>
  );
}
export default Footer;
