import React, { useState, useEffect } from "react";
import Slides from "./Slides";
import first from "../images/carouselMain/first.jpg";
import second from "../images/carouselMain/second.jpg";
import third from "../images/carouselMain/third.png";
import { MDBCarousel, MDBCarouselInner, MDBContainer } from "mdbreact";
import data from "../Info.json";
function createSlides(slides, index) {
  var sources = first;
  if (index === 0) {
  } else if (index === 1) {
    sources = second;
  } else {
    sources = third;
  }

  return (
    <Slides
      slide={slides.slide}
      source={sources}
      heading={slides.heading}
      content={slides.content}
    />
  );
}
function Slider() {
  const [lineState, setLineState] = useState({
    rendered: null,
  });
  useEffect(() => {
    fetch("https://www.houstonzoo.org/wp-json/wp/v2/posts/84645")
      .then((response) => response.json())
      .then((data) => setLineState({ rendered: data.title.rendered }));
  }, []);
  return (
    <MDBContainer>
      <MDBCarousel
        activeItem={1}
        length={3}
        showControls={true}
        showIndicators={true}
        className="z-depth-1"
      >
        <MDBCarouselInner>
          {data.createSlide.map(createSlides)}
          <Slides
            slide={3}
            source={third}
            heading={lineState.rendered}
            content="At the Jem's Zoo, we work hard to make our operations environmentally friendly to ensure we’re doing our part to save animals in the wild"
          />
        </MDBCarouselInner>
      </MDBCarousel>
    </MDBContainer>
  );
}
export default Slider;
